import * as React from "react";
import { FunctionComponent, useMemo, type CSSProperties } from "react";
import * as styles from "./arrow.module.css";

type ArrowType = {
  arrowDropDown?: string;

  /** Style props */
  arrowPosition?: CSSProperties["position"];
};

const Arrow: FunctionComponent<ArrowType> = ({
  arrowDropDown,
  arrowPosition,
}) => {
  const arrowStyle: CSSProperties = useMemo(() => {
    return {
      position: arrowPosition,
    };
  }, [arrowPosition]);

  return (
    <div className={styles.arrow} style={arrowStyle}>
      <img className={styles.arrowdropdownIcon} alt="" src={arrowDropDown} />
    </div>
  );
};

export default Arrow;
